<template>

    <div class="agreement-organisation">

        <template v-if="success">

            <div class="agreement-result">

                <div class="agreement-result-icon">
                    <span class="icon-double-checkmark"></span>
                </div>

                <div class="h3">Успешно!</div>

            </div>

        </template>

        <template v-else>

            <div class="h3">Добрый день, <br>{{person ? person.name : ''}}!</div>

            <p>Для организаций {{person ? person.company.name : ''}} оформлена кредитная программа. Для оформление заявки, необходимо ваше согласование, как одного из учредителей.</p>

            <div class="signing-documents-list">

                <div class="h4">Ознакомиться с документами</div>

                <div v-for="doc in finalDocsList"
                     :key="doc.id"
                     class="upload-documents-item">

                    <div class="upload-documents-img">
                        <div class="upload-documents-poster">
                            {{doc.files[0].format}}
                        </div>
                    </div>

                    <div class="upload-documents-title">
                        <a href="#" @click.prevent="downloadFile(doc)">{{doc.name}}</a>
                        <div class="upload-documents-links">
                            <span>{{listQnt(doc.files.length)}}</span> – <a href="#" @click.prevent="downloadFile(doc)">Скачать</a>
                        </div>
                    </div>

                </div>

            </div>

            <button class="btn btn-block btn-lg"
                    :class="{'loading': load}"
                    :disabled="load"
                    @click="initDiadocScript">Подписать</button>

            <div class="footer-text">Подписание осуществляется при помощи ЭП</div>

        </template>

        <Popup v-if="diadocModal"
               ref="diadocModal"
               @close-modal="this.diadocModal = false"
               classes="diadoc-plugins-modal">

            <div class="popup-title">
                <div class="h2">Для подписания документов при помощи ЭЦП Вам необходимо:</div>
            </div>

            <div class="popup-body">
                <div class="plugins-list">
                    <div class="plugins-list-item">1. Установить криптопровайдер: <br><a href="https://www.cryptopro.ru/products/cades/plugin" class="link" target="_blank">КриптоПро CSP</a></div>
                    <div class="plugins-list-item">2. Установите КриптоПро ЭЦП Browser plug-in: <br><a href="https://cryptopro.ru/products/cades/plugin/get_2_0" class="link" target="_blank">КриптоПро CSP Browser plug-in</a></div>
                </div>
            </div>

        </Popup>

        <Popup v-if="sertificatesModal"
               ref="sertificatesModal"
               @close-modal="sertificatesModal = false"
               classes="sertificates-modal">

            <div class="popup-title">
                <div class="h2">Выберите необходимый сертификат для цифровой подписи документов:</div>
            </div>

            <div class="popup-body">
                <div class="select-sertificates">
                    <div v-for="sertificate in sertificates"
                         :key="sertificate._cadesCertificate.objid"
                         class="sertificates-item"
                         :class="{'active': selectedSertificates == sertificate}"
                         @click="selectedSertificates = sertificate">
                        <span>{{sertificate.name}} (действителен до: {{parseDate(sertificate.validTo)}})</span>
                    </div>
                </div>
                <button class="btn btn-block"
                        :disabled="signLoad"
                        :class="{'loading': signLoad}"
                        @click="createSignature">Подписать</button>
            </div>

        </Popup>

        <Popup v-if="errorDiadocModal"
               :error="true"
               :classes="'error-modal'"
               :zIndex="101"
               @close-modal="errorDiadocModal = false">

            <div class="popup-title">
                <div class="h3">Ошибка</div>
            </div>

            <div class="popup-body">
                <p class="error-text">{{errorDiadocText}}</p>
                <button class="btn btn-lg btn-block close-modal">Попробовать снова</button>
            </div>

        </Popup>

    </div>

</template>

<script>
    import { getSystemInfo, getUserCertificates, createHash, createAttachedSignature } from 'crypto-pro'
    import moment from "moment";
    import {mapState} from 'vuex'

    import Popup from "@/components/Blocks/Popup";

    export default {
        name: "AgreementOrganisitionMain",
        components: {
            Popup
        },
        data: () => ({
            load: false,
            signLoad: false,
            sertificates: [],
            selectedSertificates: '',
            sertificatesModal: false,
            diadocModal: false,
            downloadLoading: false,
            success: false,
            orderId: '',
            errorDiadocModal: false,
            errorDiadocText: ''
        }),
        computed: {
            ...mapState({
                person: state => state.agreement.personFinish,
                finalDocsList: state => state.agreement.docs
            })
        },
        methods: {
            listQnt(i) {
                if (i == 1) {
                    return `${i} лист`
                }
                else if (i > 1 && i < 5) {
                    return `${i} листа`
                }
                else {
                    return `${i} листов`
                }
            },
            parseDate(date) {
                return moment(date).format('DD.MM.YYYY')
            },
            initDiadocScript() {
                if ( !this.load ) {
                    this.load = true
                    getSystemInfo().then(res => {
                        this.load = false
                        getUserCertificates().then(sertList => {
                            if ( sertList.length > 0 ) {
                                let sertPromises = []
                                this.sertificates = []

                                sertList.forEach(sert => {
                                    sertPromises.push(new Promise(resolve => {
                                        sert.isValid().then(() => {
                                            this.sertificates.push(sert)
                                            resolve();
                                        })
                                    }))
                                })

                                Promise.all(sertPromises).then(() => {
                                    this.load = false
                                    this.selectedSertificates = this.sertificates[0]
                                    this.sertificatesModal = true
                                })
                            }
                        }).catch(res => {
                            switch (res) {
                                case 'certificates_not_found':
                                    this.errorDiadocText = 'Не найдено ни одного сертификата.'
                                    break;
                                case 'valid_certificates_not_found':
                                    this.errorDiadocText = 'Не найдено ни одного валидного сертификата.'
                                    break;
                                case 'certificate_read_error':
                                    this.errorDiadocText = 'Ошибка чтения одного из сертификатов.'
                                    break
                            }
                            this.errorDiadocModal = true
                        })
                    }).catch(error => {
                        this.load = false
                        this.diadocModal = true
                    })
                }
            },
            readFile(messageFile) {
                return new Promise(function (resolve) {
                    let fileReader = new FileReader();
                    fileReader.onload = function () {
                        resolve(this.result);
                    };
                    fileReader.readAsArrayBuffer(messageFile);
                });
            },
            generateSignature(hash) {
                const sert = this.selectedSertificates
                let signaturePromise;
                return new Promise((resolve, reject) => {signaturePromise = createAttachedSignature(sert.thumbprint, hash)
                    signaturePromise.then(function (signature) {
                        resolve(signature)
                    }, function (error) {
                        this.errorDiadocText = error.message
                        this.errorDiadocModal = true
                        this.signLoad = false
                        reject()
                    }).catch(error => {
                        this.errorDiadocText = error.message
                        this.errorDiadocModal = true
                        this.signLoad = false
                    })
                })
            },
            createSignature() {
                let dataArr = this.selectedSertificates.subjectName.split(',')
                dataArr = dataArr.map(item => item.trim().split('='))
                // const sertInnUl = dataArr.filter(item => item[0] === 'ИНН ЮЛ')[0]
                // const sertInn = parseInt(dataArr.filter(item => item[0] === 'ИНН')[0][1]).toString()
                // if (!sertInnUl) {
                //     if (sertInn !== this.company.inn) {
                //         this.errorDiadocText = 'Вы выбрали сертификат, который не принадлежит компании'
                //         this.errorDiadocModal = true
                //         return false
                //     }
                // } else {
                //     const sertInnUlParsed = parseInt(sertInnUl[1]).toString()
                //     if (sertInnUlParsed !== this.company.inn) {
                //         this.errorDiadocText = 'Вы выбрали сертификат, который не принадлежит компании'
                //         this.errorDiadocModal = true
                //         return false
                //     }
                // }
                this.signLoad = true
                let ths = this
                let signaturePromises = []
                let sendData = {
                    orderId: this.orderId,
                    token: this.getParametr,
                    docs: []
                }
                this.finalDocsList.forEach(finalDocsListItem => {
                    let doc = {
                        docId: finalDocsListItem.id,
                        files: []
                    }
                    finalDocsListItem.files.forEach(file => {
                        signaturePromises.push(new Promise((resolve, reject) => {
                            this.$store.dispatch('getFileWithoutToken', {token: this.getParametr, fileId: file.id}).then(resp => {
                                let fileObj = new File([resp], file.name, {type: file.format})
                                let messagePromise = this.readFile(fileObj)
                                messagePromise.then(message => {
                                    createHash(message).then(function() {
                                        ths.generateSignature(message).then(signature => {
                                            doc.files.push({
                                                id: file.id,
                                                signature
                                            })
                                            resolve()
                                        }).catch(() => {
                                            this.signLoad = false
                                        })
                                    }, function(error) {
                                        this.errorDiadocText = error.message
                                        this.errorDiadocModal = true
                                        this.signLoad = false
                                        reject()
                                    })
                                })
                            })
                        }))
                    })
                    sendData.docs.push(doc)
                })
                Promise.all(signaturePromises).then(() => {
                    this.$store.dispatch('sendSignatureForFoundersFinish', sendData).then(() => {
                        this.signLoad = false
                        this.$refs.sertificatesModal.closePopup()
                        this.successModal = true
                        this.success = true
                    })
                }).catch(() => this.signLoad = false)
            },
            downloadFile (doc) {
                doc.files.forEach(file => {
                    this.$store.dispatch('getFile', file.id).then(blob => {
                        console.log(blob)
                        this.$store.dispatch('downloadFile', {content: blob, name: file.name, format: file.format})
                    })
                })
            }
        },
        mounted() {
            const params = window
                .location
                .search
                .replace('?','')
                .split('&')
                .reduce(
                    function(p,e){
                        const a = e.split('=');
                        p[ decodeURIComponent(a[0])] = decodeURIComponent(a[1]);
                        return p;
                    },
                    {}
                );
            this.getParametr = params['c']
            this.orderId = params['id']
            this.$store.dispatch('getPersonDataFinish',  {token: this.getParametr, orderId: this.orderId})
            this.$store.dispatch('getAgreementDocsFinish', {token: this.getParametr, orderId: this.orderId})
        }
    }
</script>

